











/* eslint-disable comma-dangle */
import { Component, Prop, Vue, Watch } from 'vue-property-decorator'

@Component({})
export default class Selector extends Vue {
  @Prop() options!: string[];
  @Prop() value!: string;
  val = ''
  custom = 'https://'

  @Watch('value')
  changeVal (v: string) {
    if (this.options.indexOf(v) === -1) {
      this.custom = v
    }
    this.val = v
  }

  @Watch('val')
  @Watch('custom')
  changeValue (v: string) {
    this.$emit('input', v)
  }

  mounted () {
    this.changeVal(this.value)
  }
}
